//or import a user-define reusable variable file
@import 'Variables.scss';

// nested css --> not possible in normal css
.container{
    display: flex;
    justify-content: center;
    background-color: $defcolor;
    .heading{
        color: #E97777;
    }
    .desc{
        color: #9E7676;
    }
    .button{
        color: #80489C;
        font-weight: bolder;
        background-color: #9ED5C5;
    }
}

.child{
    display: flex;
    justify-content: center;
    background-color: $defcolor;
}



